import styled from 'styled-components';

export const Container = styled.div`
    display: flex;
    flex: 1;
    height: 100%;
    width: 100%;
    background: #2B2B2B;
    padding-bottom: 100px;
`;

export const Row = styled.div`
    margin: 20px 20%;
    display: flex;
    flex-direction: row;

    @media (max-width: 1024px) {
        flex-direction: column;
    }

`;

export const Avatar = styled.div`
    margin: 30px;

    @media (max-width: 1024px) {
        margin: 10px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
`;

export const AvatarImg = styled.img`
    height: 120px;
    width: 120px;
    border-radius: 100%;
    /* border: 3px solid; */
    /* background: #eee; */
`;

export const About = styled.div`

    padding: 0 50px;

    h2 {
        color: #FFF;
        margin-bottom: 30px;
    }

    p { 
        color: #eee;
        display: flex;
        justify-content: center;
        letter-spacing: 1px;
    }
`;
